import React, { useState } from "react";
import "./Contact.css";
import line_consult from "./images/line_consult.png";
import line_base from "./images/line_base.png";
import MediaQuery from "react-responsive";

function Contact() {
  const [status, setStatus] = useState("Submit");

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("buttom pushed");
    setStatus("Sending...");
    const { name, email, tel, message, options } = e.target.elements;

    let details = {
      name: name.value,
      email: email.value,
      tel: tel.value,
      message: message.value,
      options: options.value,
    };

    let response = await fetch(
      "https://polymath-express.wl.r.appspot.com/contact",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json;charset=utf-8",
        },
        body: JSON.stringify(details),
      }
    );
    setStatus("Submit");
    let result = await response.json();
    alert(result.status);
  };

  return (
    <form onSubmit={handleSubmit}>
      <div className="form-wrapper">
        <MediaQuery query="(max-width: 767px)">
          <p className="small-letter smartphone-contact">
            
          </p>
          <div className="mb-1">
            <input
              type="text"
              id="name"
              placeholder=""
              className="smartphone-contact"
              required
            />
          </div>
          <div className="mb-1">
            <input
              type="email"
              id="email"
              placeholder=""
              className="smartphone-contact"
              required
            />
          </div>
          <div className="mb-1">
            <input
              type="text"
              id="tel"
              placeholder=""
              className="smartphone-contact"
              required
            />
          </div>
          <div className="mb-1">
            <textarea
              id="message"
              placeholder=""
              className="smartphone-contact"
              required
            ></textarea>
          </div>
          <div>
            <select
              id="options"
              required
              className="mt-3 option-font smartphone-contact"
            >
              <option value="" disabled selected required>
                *
              </option>
              <option value="Twitter" required>
                Twitter
              </option>
              <option value="Facebook" required>
                Facebook
              </option>
              <option value="Youtube" required>
                Youtube
              </option>
              <option value="Note" required>
                Note
              </option>
              <option value="" required>
                
              </option>
              <option value="" required>
                
              </option>
              <option value="" required>
                
              </option>
              <option value="" required>
                
              </option>
            </select>
          </div>
          <button type="submit" className="mt-3 mb-3 button">
            {status}
          </button>

          <p className="small-letter smartphone-contact">
            LINE
          </p>

          {/* a line logo including a text */}
          <a href="https://lin.ee/Rz2NZubS" target="_blank">
            <div className="line-wrapper">
              <img src={line_consult} className="line-logo" />
            </div>
          </a>

          {/* a line logo using an original picuture and text*/}
          {/* <a href="https://lin.ee/Rz2NZubS" target="_blank">
              <div className="line-wrapper">
                <div className="line-relative">
                  <img src={line_base} className="line-logo" />
                  <div className="line-absolute">LINE</div>
                </div>
              </div>
            </a> */}
        </MediaQuery>

        <MediaQuery query="(min-width: 768px)">
          <div className="contact-wrapper-pc">
            <p className="text-center small-letter pc">
              
            </p>
            <div className="mb-1">
              <input
                type="text"
                id="name"
                placeholder=""
                className="pc"
                required
              />
            </div>
            <div className="mb-1">
              <input
                type="email"
                id="email"
                placeholder=""
                className="pc"
                required
              />
            </div>
            <div className="mb-1">
              <input
                type="text"
                id="tel"
                placeholder=""
                className="pc"
                required
              />
            </div>
            <div className="mb-1">
              <textarea
                id="message"
                placeholder=""
                className="pc"
                required
              ></textarea>
            </div>
            <div>
              <select required id="options" className="mt-3 option-font pc">
                <option required selected value="">
                  {" "}
                  *
                </option>
                <option required value="Twitter">
                  Twitter
                </option>
                <option required value="Facebook">
                  Facebook
                </option>
                <option required value="Youtube">
                  Youtube
                </option>
                <option required value="Note">
                  Note
                </option>
                <option required value="">
                  
                </option>
                <option required value="">
                  
                </option>
                <option required value="">
                  
                </option>
                <option required value="">
                  
                </option>
              </select>
            </div>
            <button type="submit" className="mt-3 mb-3 button">
              {status}
            </button>
          </div>

          <div>
            <p className="text-center small-letter pc">
              LINE
            </p>
            <a href="https://lin.ee/Rz2NZubS" target="_blank">
              <div className="line-wrapper">
                <img src={line_consult} className="line-logo" />
              </div>
            </a>
            {/* <a href="https://lin.ee/Rz2NZubS" target="_blank">
              <div className="line-wrapper">
                <div className="line-relative">
                  <img src={line_base} className="line-logo" />
                  <div className="line-absolute">LINE</div>
                </div>
              </div>
            </a> */}
          </div>
        </MediaQuery>
      </div>
    </form>
  );
}

export default Contact;
